
"use client";

import { useState } from "react";
import { drivers as initialDrivers } from "@/lib/data";
import { DataTable } from "@/components/data-table/data-table";
import { columns } from "./columns";
import { PageHeader } from "@/components/page-header";
import { Button } from "@/components/ui/button";
import { PlusCircle } from "lucide-react";
import { CreateDriverDialog } from "@/components/create-driver-dialog";
import type { Driver } from "@/lib/types";

export default function DriversPage() {
  const [drivers, setDrivers] = useState<Driver[]>(initialDrivers);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [selectedDriver, setSelectedDriver] = useState<Driver | null>(null);

  const handleCreateNew = () => {
    setSelectedDriver(null);
    setIsDialogOpen(true);
  };

  const handleEdit = (driver: Driver) => {
    setSelectedDriver(driver);
    setIsDialogOpen(true);
  };

  const handleDelete = (driverId: string) => {
    if (!window.confirm("Are you sure you want to delete this driver? This action cannot be undone.")) {
      return;
    }
    setDrivers(prev => prev.filter(d => d.id !== driverId));
    // In a real app, you would also call an API to delete from the database
  };

  const handleDialogSubmit = (driverData: Driver) => {
    if (selectedDriver) {
      // Update
      setDrivers(prev => prev.map(d => (d.id === selectedDriver.id ? driverData : d)));
    } else {
      // Create
      setDrivers(prev => [driverData, ...prev]);
    }
    setSelectedDriver(null);
  };

  return (
    <div>
       <PageHeader 
        title="Driver Management" 
        description="View and manage all drivers in your organization."
        action={
          <Button onClick={handleCreateNew}>
            <PlusCircle className="mr-2 h-4 w-4" />
            Add Driver
          </Button>
        }
      />
      <DataTable columns={columns({ onEdit: handleEdit, onDelete: handleDelete })} data={drivers} />
      <CreateDriverDialog
        isOpen={isDialogOpen}
        onOpenChange={setIsDialogOpen}
        onDriverSubmit={handleDialogSubmit}
        driver={selectedDriver}
      />
    </div>
  );
}
